
.detail-wrap{
      width:1200px;
      margin:0 auto;
      padding-top: 20px;
      overflow: hidden;
}
.detail-left{
      float:left;
      width:200px;

      background:#fff;
      padding:20px 0px;
      text-align:center;
}
.detail-right{
      margin-left:20px;
      float:left;
      width:980px;
}
.detail-left ul{
      text-align:left;
      margin-top:20px;
}
.detail-left li{
      padding:10px 15px;
      cursor:pointer;
}
.detail-left li:hover{
      background: #4fc08d;
      color: #fff;
}
.sales-board {
  background: #fff;
}
.sales-board-form {
}
.sales-board-intro h2 {
  font-size: 20px;
  padding: 20px;
}
.sales-board-intro p {
  background: #f7fcff;
  padding: 10px 20px;
  color: #999;
  line-height: 1.8;
}
.sales-board-form {
  padding: 30px 20px;
  font-size: 14px;
}
.sales-board-line {
  clear: both;
  padding-bottom: 20px;
}
.sales-board-line-left {
    display: inline-block;
    width: 100px;
}
.sales-board-line-right {
    display: inline-block;
    width: 75%;
}
.sales-board-des {
  border-top: 20px solid #f0f2f5;
  padding: 15px 20px;
}
.sales-board-des p {
  line-height: 1.6;
}
.sales-board-des h2 {
  font-size: 20px;
  padding-bottom: 15px;
}
.sales-board-des h3 {
  font-size: 18px;
  font-weight: bold;
  padding: 20px 0 10px 0;
}
.sales-board-des li {
  padding: 5px 0;
}
.sales-board-table {
  width: 100%;
  margin-top: 20px;
}
.sales-board-table th {
  background: #4fc08d;
  color: #fff;
}
.sales-board-table td {
    border: 1px solid #f0f2f5;
    padding: 15px;
}



